import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import './App.css';

import FlatButton from 'material-ui/FlatButton'
import Display from './Display'

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      notes: ['A','A#','B','C','C#',
              'D','D#','E','F','F#',
              'G','G#'],
      currNote: 'A'
    }
  }
  buttonStyle = {
    minWidth: 30,
    //color: 'white'
  }

  notePressed = (item) => (event) => {
    console.log(item);
    if (item === this.state.currNote) {
      let newNote = this.state.notes[Math.floor(Math.random()*12)];
      while (newNote == this.state.currNote) {
        console.log("DUPED");
        newNote = this.state.notes[Math.floor(Math.random()*12)];
      }
      this.setState({currNote: newNote});
    }
  }

  render() {
    return (
      <div className="App">
        < MuiThemeProvider >
          <div className="main-view">
          <Display currNote={this.state.currNote}/>
            <table className="neck-table">
              <tr class="open-strings">
                <td class="row-1"><FlatButton onClick={this.notePressed("G")} fullWidth={false} style={this.buttonStyle}>G</FlatButton></td>
                <td class="row-1"><FlatButton onClick={this.notePressed("D")} fullWidth={false} style={this.buttonStyle}>D</FlatButton></td>
                <td class="row-1"><FlatButton onClick={this.notePressed("A")} fullWidth={false} style={this.buttonStyle}>A</FlatButton></td>
                <td class="row-1"><FlatButton onClick={this.notePressed("E")} fullWidth={false} style={this.buttonStyle}>E</FlatButton></td>
              </tr>
              <tr>
                <td class="row-2"><FlatButton onClick={this.notePressed("G#")} fullWidth={false} style={this.buttonStyle}>G#</FlatButton></td>
                <td class="row-2"><FlatButton onClick={this.notePressed("D#")} fullWidth={false} style={this.buttonStyle}>D#</FlatButton></td>
                <td class="row-2"><FlatButton onClick={this.notePressed("A#")} fullWidth={false} style={this.buttonStyle}>A#</FlatButton></td>
                <td class="row-2"><FlatButton onClick={this.notePressed("F")} fullWidth={false} style={this.buttonStyle}>F</FlatButton></td>
              </tr>
              <tr>
                <td class="row-3"><FlatButton onClick={this.notePressed("A")} fullWidth={false} style={this.buttonStyle}>A</FlatButton></td>
                <td class="row-3"><FlatButton onClick={this.notePressed("E")} fullWidth={false} style={this.buttonStyle}>E</FlatButton></td>
                <td class="row-3"><FlatButton onClick={this.notePressed("B")} fullWidth={false} style={this.buttonStyle}>B</FlatButton></td>
                <td class="row-3"><FlatButton onClick={this.notePressed("F#")} fullWidth={false} style={this.buttonStyle}>F#</FlatButton></td>
              </tr>
              <tr>
                <td class="row-4"><FlatButton onClick={this.notePressed("A#")} fullWidth={false} style={this.buttonStyle}>A#</FlatButton></td>
                <td class="row-4"><FlatButton onClick={this.notePressed("F")} fullWidth={false} style={this.buttonStyle}>F</FlatButton></td>
                <td class="row-4"><FlatButton onClick={this.notePressed("C")} fullWidth={false} style={this.buttonStyle}>C</FlatButton></td>
                <td class="row-4"><FlatButton onClick={this.notePressed("G")} fullWidth={false} style={this.buttonStyle}>G</FlatButton></td>
              </tr>
              <tr>
                <td class="row-5"><FlatButton onClick={this.notePressed("B")} fullWidth={false} style={this.buttonStyle}>B</FlatButton></td>
                <td class="row-5"><FlatButton onClick={this.notePressed("F#")} fullWidth={false} style={this.buttonStyle}>F#</FlatButton></td>
                <td class="row-5"><FlatButton onClick={this.notePressed("C#")} fullWidth={false} style={this.buttonStyle}>C#</FlatButton></td>
                <td class="row-5"><FlatButton onClick={this.notePressed("G#")} fullWidth={false} style={this.buttonStyle}>G#</FlatButton></td>
              </tr>
              <tr>
                <td class="row-6"><FlatButton onClick={this.notePressed("C")} fullWidth={false} style={this.buttonStyle}>C</FlatButton></td>
                <td class="row-6"><FlatButton onClick={this.notePressed("G")} fullWidth={false} style={this.buttonStyle}>G</FlatButton></td>
                <td class="row-6"><FlatButton onClick={this.notePressed("D")} fullWidth={false} style={this.buttonStyle}>D</FlatButton></td>
                <td class="row-6"><FlatButton onClick={this.notePressed("A")} fullWidth={false} style={this.buttonStyle}>A</FlatButton></td>
              </tr>
            </table>
          </div>
        </MuiThemeProvider>
      </div>
      
    );
  }
}

export default App;



